/* FONTS */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap');

/* MEYER'S RESET */
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* CUSTOM RESET AND VARIABLES */
:root {
  color: #222222;
  font-family: 'Roboto', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-size: 10px;

  --blue_lightest: #d9dff1;
  --blue_light: #a4aac4;
  --blue_medium: #3546ab;
  --blue_dark: #0c135b;
  --green: #35ab70;
  --grey_light: #f8f9fe;
  --grey_medium: #c7c7c7;
  --grey_dark: #222222;
  --orange_light: #fde7e0;
  --orange_medium: #fd9579;
  --orange_gradient: linear-gradient(to bottom, #ff8562, #f0582e);
  --red: #d13f3f;
  --white: #ffffff;

  --phone: 600px;
  --tablet: 768px;
  --desktop: 1024px;
  --large: 1280px;
}

.view {
  background-color: var(--grey_light);
  min-height: calc(100vh - 12rem);
}

.main {
  padding: 2.4rem;
}

@media only screen and (min-width: 768px) {
  .main {
    padding: 4rem;
    max-width: 70rem;
    margin: auto;
  }
}

@media only screen and (min-width: 1024px) {
  .view {
    grid-column: 2 / 3;
    grid-row: 2 / 3;

    height: calc(100vh - 12rem);
    overflow-y: scroll;
  }

  .main {
    padding: 0;
    margin: 0;
    max-width: unset;
  }
}
